:root {
  --bg: #1a1d24;
  --surface: rgba(28, 32, 42, 0.92);
  --surface-border: rgba(80, 95, 120, 0.4);
  --text: #e8ecf2;
  --muted: #8b9ab0;
  --accent: #5b9cf0;
  --accent-dim: #3d6ba8;
  --success: #6bc98a;
  --warn: #e8b85c;
  --line: rgba(120, 140, 170, 0.35);
}
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  min-height: 100vh;
  height: 100%;
  font-family: "DM Sans", "Segoe UI", system-ui, sans-serif;
  background: var(--bg);
  color: var(--text);
  overflow: hidden;
}
.game-view {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.game-viewport {
  position: absolute;
  inset: 0;
}
.clinic-scene-3d {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}
.hud {
  position: absolute;
  pointer-events: none;
}
.hud > * {
  pointer-events: auto;
}
.hud-top {
  top: 0;
  left: 0;
  right: 0;
  z-index: 80;
  padding: 16px 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, transparent 100%);
}
.hud-header {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.hud-title {
  font-size: 1.35rem;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: var(--text);
}
.hud-subtitle {
  font-size: 0.8rem;
  color: var(--muted);
  letter-spacing: 0.04em;
}
.hud-case-picker {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: -18px;
}
.emoji-key-wrap {
  position: relative;
}
.top-tools {
  margin-left: auto;
  display: inline-flex;
  align-items: flex-start;
  gap: 8px;
}
.floating-tools {
  position: absolute;
  top: 16px;
  right: 20px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  z-index: 120;
  pointer-events: auto;
}
.doctor-chat-launch {
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  background: rgba(20, 24, 32, 0.9);
  color: var(--text);
  padding: 8px 12px;
  font-size: 0.82rem;
  font-weight: 700;
  cursor: pointer;
}
.doctor-chat-launch:hover {
  border-color: var(--accent);
}
.emoji-key-button {
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  background: rgba(20, 24, 32, 0.9);
  color: var(--text);
  width: 38px;
  height: 38px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  font-size: 1.05rem;
  font-weight: 600;
  line-height: 1;
  cursor: pointer;
}
.emoji-key-button:hover {
  border-color: var(--accent);
}
.emoji-key-panel {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  width: 230px;
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  background: rgba(18, 23, 31, 0.97);
  padding: 10px 12px;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.3);
  z-index: 50;
}
.emoji-key-title {
  margin: 0 0 6px;
  font-size: 0.78rem;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #c7d4e8;
  font-weight: 700;
}
.emoji-key-item {
  margin: 0;
  color: var(--muted);
  font-size: 0.8rem;
  line-height: 1.35;
}
.emoji-key-item + .emoji-key-item {
  margin-top: 3px;
}
.doctor-wrap {
  position: relative;
}
.doctor-panel {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  width: 320px;
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  background: rgba(18, 23, 31, 0.98);
  padding: 10px 12px;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.34);
  z-index: 55;
}
.doctor-inline-panel {
  flex: 0 0 auto;
}
.doctor-inline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
}
.doctor-title {
  margin: 0;
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text);
}
.doctor-subtitle {
  margin: 2px 0 8px;
  font-size: 0.75rem;
  color: var(--muted);
}
.doctor-chat {
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid rgba(80, 95, 120, 0.35);
  border-radius: 8px;
  padding: 8px;
  background: rgba(10, 14, 20, 0.55);
}
.doctor-message {
  margin: 0;
  font-size: 0.8rem;
  line-height: 1.35;
  white-space: pre-wrap;
}
.doctor-message + .doctor-message {
  margin-top: 8px;
}
.doctor-message.user {
  color: #cfe3ff;
}
.doctor-message.assistant {
  color: var(--muted);
}
.doctor-input-row {
  margin-top: 8px;
  display: grid;
  gap: 6px;
}
.doctor-input {
  width: 100%;
  resize: vertical;
  min-height: 54px;
  border: 1px solid var(--surface-border);
  border-radius: 8px;
  background: rgba(13, 17, 24, 0.9);
  color: var(--text);
  padding: 8px;
  font-size: 0.82rem;
}
.doctor-input:focus {
  outline: none;
  border-color: var(--accent);
}
.doctor-send {
  justify-self: end;
  border: 1px solid var(--accent-dim);
  background: rgba(61, 107, 168, 0.3);
  color: var(--text);
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 0.82rem;
  font-weight: 700;
  cursor: pointer;
}
.mode-picker {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  gap: 8px;
}
.mode-select {
  min-width: 170px;
  max-width: 220px;
  padding: 6px 10px;
  font-size: 0.82rem;
  border-radius: 9px;
  border: 1px solid var(--surface-border);
  background: var(--surface);
  color: var(--text);
}
.mode-select:hover,
.mode-select:focus {
  border-color: var(--accent);
  outline: none;
}
.mode-switch {
  display: inline-flex;
  flex-wrap: wrap;
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  overflow: hidden;
  background: rgba(20, 24, 32, 0.85);
}
.mode-switch button {
  border: 0;
  background: transparent;
  color: var(--muted);
  padding: 8px 12px;
  font-size: 0.82rem;
  font-weight: 700;
  cursor: pointer;
}
.mode-switch button.active {
  background: rgba(91, 156, 240, 0.2);
  color: var(--text);
}
.quiz-progress {
  margin: 0;
  font-size: 0.88rem;
  color: var(--text);
  font-weight: 600;
}
.case-label {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--muted);
  white-space: nowrap;
}
.hud-case-picker select {
  min-width: 220px;
  padding: 10px 14px;
  border-radius: 10px;
  border: 1px solid var(--surface-border);
  background: var(--surface);
  color: var(--text);
  font-size: 0.95rem;
  cursor: pointer;
  backdrop-filter: blur(8px);
}
.hud-case-picker select:hover,
.hud-case-picker select:focus {
  border-color: var(--accent);
  outline: none;
}
.case-picker-select {
  min-width: 170px;
  max-width: 220px;
  padding: 6px 10px;
  font-size: 0.82rem;
  border-radius: 9px;
}
.hud-left {
  left: 0;
  top: 54%;
  transform: translateY(-50%);
  padding: 0 16px;
  max-width: 320px;
  max-height: calc(100vh - 120px);
  width: min(320px, 32vw);
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow: hidden;
}
.hud-right {
  right: 0;
  top: 50%;
  transform: translateY(calc(-50% + 28px));
  padding: 0 16px;
  width: min(420px, 36vw);
  max-height: calc(100vh - 120px);
  height: calc(100vh - 120px);
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow: hidden;
}
.level-status-panel {
  margin-top: 0;
  padding: 10px 12px;
  flex: 0 0 auto;
}
.level-status-panel .feedback-label {
  margin-bottom: 6px;
}
.level-status-panel .headline {
  margin: 0;
  text-transform: capitalize;
}
.level-status-panel .score {
  margin-top: 4px;
}
.hud-bottom {
  right: 16px;
  left: auto;
  bottom: max(10px, env(safe-area-inset-bottom));
  transform: none;
  width: min(320px, 29vw);
  max-height: min(44vh, 420px);
  z-index: 85;
}
.hud-panel {
  background: var(--surface);
  border: 1px solid var(--surface-border);
  border-radius: 14px;
  padding: 14px 16px;
  backdrop-filter: blur(12px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.25);
  overflow-y: auto;
}
.info-panel h2 {
  margin: 0 0 6px;
  font-size: 1.05rem;
  font-weight: 700;
}
.info-panel p {
  margin: 0;
  font-size: 0.9rem;
  color: var(--muted);
  line-height: 1.4;
}
.condition-line {
  margin-top: 8px !important;
  font-size: 0.82rem !important;
}
.stats-title {
  margin: 12px 0 8px;
  font-size: 0.82rem;
  font-weight: 800;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--muted);
}
.quick-tags {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}
.quick-tag {
  display: inline-flex;
  align-items: center;
  padding: 5px 8px;
  border-radius: 999px;
  font-size: 0.74rem;
  font-weight: 700;
  letter-spacing: 0.01em;
}
.quick-tag.warn {
  background: rgba(232, 184, 92, 0.2);
  border: 1px solid rgba(232, 184, 92, 0.35);
  color: #ffd48d;
}
.quick-tag.good {
  background: rgba(107, 201, 138, 0.2);
  border: 1px solid rgba(107, 201, 138, 0.35);
  color: #a9e7bd;
}
.quick-tag.info {
  background: rgba(91, 156, 240, 0.2);
  border: 1px solid rgba(91, 156, 240, 0.35);
  color: #b7d6ff;
}
.info-panel {
  flex: 1 1 auto;
  min-height: 0;
  max-height: none;
}
.drug-panel {
  flex: 1 1 56%;
  min-height: 42vh;
  max-height: none;
}
.feedback-panel {
  flex: 1 1 34%;
  min-height: 24vh;
  max-height: none;
}
.bottom-prompt-panel {
  min-height: 280px;
  max-height: min(42vh, 420px);
  display: grid;
  gap: 8px;
  overflow-y: auto;
}
.bottom-prompt-panel .headline {
  margin: 0;
  font-size: 0.92rem;
}
.bottom-prompt-panel .rationale {
  margin-top: 0;
  font-size: 0.82rem;
  line-height: 1.3;
}
.bottom-prompt-panel .doctor-input {
  min-height: 86px;
  max-height: 130px;
  width: 100%;
  justify-self: stretch;
}
.bottom-prompt-panel .quiz-actions {
  margin-top: 4px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.bottom-prompt-panel .quiz-actions button {
  padding: 7px 10px;
  font-size: 0.8rem;
}
.vitals-grid {
  margin-top: 12px;
  display: grid;
  gap: 6px;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
.vitals-grid span {
  background: rgba(60, 70, 90, 0.5);
  padding: 8px 10px;
  border-radius: 8px;
  font-size: 0.82rem;
  color: var(--text);
  border: 1px solid rgba(80, 95, 120, 0.2);
  line-height: 1.35;
}
.vitals-grid strong {
  display: block;
  color: var(--muted);
  font-size: 0.72rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.drug-panel h3 {
  margin: 0 0 10px;
  font-size: 0.95rem;
  font-weight: 700;
}
.question-type-switch {
  display: inline-flex;
  border: 1px solid var(--surface-border);
  border-radius: 10px;
  overflow: hidden;
  background: rgba(20, 24, 32, 0.8);
  margin-bottom: 8px;
}
.question-type-switch button {
  border: 0;
  background: transparent;
  color: var(--muted);
  padding: 7px 10px;
  font-size: 0.78rem;
  font-weight: 700;
  cursor: pointer;
}
.question-type-switch button.active {
  background: rgba(91, 156, 240, 0.2);
  color: var(--text);
}
.hint-row {
  margin-bottom: 10px;
}
.hint-toggle {
  border: 1px solid rgba(232, 184, 92, 0.5);
  background: rgba(84, 67, 31, 0.28);
  color: #ffe4a9;
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 0.78rem;
  font-weight: 700;
  cursor: pointer;
}
.hint-text {
  margin: 8px 0 0;
  padding: 8px 10px;
  border-radius: 8px;
  border: 1px dashed rgba(232, 184, 92, 0.45);
  background: rgba(76, 62, 30, 0.18);
  color: #f7dfab;
  font-size: 0.8rem;
  line-height: 1.35;
}
.free-answer-block {
  display: grid;
  gap: 8px;
}
.reasoning-option-wrap {
  display: grid;
  gap: 6px;
}
.reasoning-composer {
  border: 1px solid rgba(91, 156, 240, 0.35);
  background: rgba(27, 36, 50, 0.45);
  border-radius: 10px;
  padding: 8px;
}
.input-error {
  margin: 0;
  color: #ffc1c1;
  font-size: 0.78rem;
}
.drug-grid {
  display: grid;
  gap: 8px;
}
.drug-option-wrap {
  display: grid;
  gap: 8px;
}
.drug-card {
  text-align: left;
  border-radius: 10px;
  border: 1px solid var(--surface-border);
  background: rgba(40, 48, 62, 0.6);
  padding: 12px 14px;
  color: var(--text);
  cursor: pointer;
  transition: transform 0.15s ease, border-color 0.2s ease, box-shadow 0.2s ease;
}
.drug-card:disabled {
  opacity: 0.58;
  cursor: not-allowed;
  transform: none;
}
.drug-card:hover {
  transform: translateX(4px);
  border-color: var(--accent-dim);
  background: rgba(50, 60, 78, 0.7);
}
.drug-card strong {
  display: block;
  margin-bottom: 4px;
  font-size: 0.95rem;
}
.drug-card span {
  color: var(--muted);
  font-size: 0.82rem;
}
.drug-name {
  margin-bottom: 8px !important;
}
.drug-meta-block + .drug-meta-block {
  margin-top: 8px;
}
.drug-label,
.feedback-label {
  margin: 0 0 3px;
  font-size: 0.72rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #c7d4e8;
  font-weight: 700;
}
.drug-meta {
  margin: 0;
  line-height: 1.35;
  color: var(--muted);
  font-size: 0.82rem;
}
.drug-list {
  margin: 0;
  padding-left: 16px;
  color: var(--muted);
  font-size: 0.8rem;
  line-height: 1.35;
}
.drug-list li + li {
  margin-top: 2px;
}
.drug-card.selected {
  border-color: var(--accent);
  box-shadow: 0 0 0 2px rgba(91, 156, 240, 0.25);
  background: rgba(50, 70, 100, 0.5);
}
.feedback-panel {
  border-color: rgba(107, 201, 138, 0.35);
  background: linear-gradient(135deg, rgba(28, 42, 35, 0.9), rgba(25, 35, 32, 0.95));
}
.inline-feedback {
  border: 1px solid rgba(107, 201, 138, 0.35);
  border-radius: 10px;
  background: linear-gradient(135deg, rgba(28, 42, 35, 0.9), rgba(25, 35, 32, 0.95));
  padding: 12px;
}
.consolidation-panel {
  display: grid;
  gap: 8px;
  align-content: center;
  text-align: center;
}
.score-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}
.feedback-chat-toggle {
  border: 1px solid var(--surface-border);
  border-radius: 8px;
  background: rgba(22, 28, 36, 0.7);
  color: var(--text);
  padding: 6px 8px;
  font-size: 0.78rem;
  font-weight: 700;
  cursor: pointer;
}
.score,
.headline,
.rationale {
  margin: 0;
}
.score {
  font-weight: 700;
  color: var(--accent);
  font-size: 1rem;
}
.headline {
  font-weight: 700;
  font-size: 0.95rem;
  color: var(--text);
}
.rationale {
  font-size: 0.88rem;
  color: var(--muted);
  margin-top: 8px;
  line-height: 1.4;
}
.feedback-panel ul,
.inline-feedback ul {
  margin: 10px 0;
  padding-left: 18px;
  font-size: 0.88rem;
  color: var(--muted);
}
.feedback-list {
  margin-top: 6px !important;
}
.evidence {
  margin: 0;
  font-size: 0.8rem;
  color: var(--muted);
  font-style: italic;
}
.side-effects {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px dashed rgba(120, 140, 170, 0.35);
}
.side-effects-title {
  margin: 0 0 6px;
  font-size: 0.8rem;
  font-weight: 700;
  color: #ffd48d;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}
.quiz-actions {
  margin-top: 10px;
}
.quiz-actions button {
  border: 1px solid var(--accent-dim);
  background: rgba(61, 107, 168, 0.25);
  color: var(--text);
  border-radius: 8px;
  padding: 8px 10px;
  font-size: 0.84rem;
  font-weight: 700;
  cursor: pointer;
}
@media (max-width: 900px) {
  .hud-left {
    left: 50%;
    top: auto;
    bottom: 165px;
    transform: translateX(-50%);
    max-width: 90%;
    max-height: 28vh;
    width: 90%;
    gap: 8px;
  }
  .hud-right {
    right: 50%;
    top: auto;
    bottom: 0;
    transform: translate(50%, 0);
    max-width: 90%;
    width: 90%;
    max-height: 56vh;
    height: 56vh;
    padding-bottom: 16px;
  }
  .hud-bottom {
    left: 50%;
    right: auto;
    bottom: max(8px, env(safe-area-inset-bottom));
    transform: translateX(-50%);
    width: calc(100vw - 16px);
    max-height: min(32vh, 240px);
  }
  .drug-panel,
  .feedback-panel,
  .info-panel,
  .bottom-prompt-panel {
    max-height: unset;
    min-height: unset;
    flex: 1 1 auto;
  }
  .bottom-prompt-panel .doctor-input {
    min-height: 58px;
    max-height: 88px;
  }
  .hud-top {
    flex-direction: column;
    align-items: flex-start;
  }
  .emoji-key-wrap {
    margin-left: 0;
  }
  .top-tools {
    margin-left: 0;
  }
  .floating-tools {
    right: auto;
    left: 20px;
    top: 78px;
  }
  .emoji-key-panel {
    right: auto;
    left: 0;
  }
  .doctor-panel {
    right: auto;
    left: 0;
    width: min(92vw, 320px);
  }
}
@property --tw-rotate-x {
  syntax: "*";
  inherits: false;
}
@property --tw-rotate-y {
  syntax: "*";
  inherits: false;
}
@property --tw-rotate-z {
  syntax: "*";
  inherits: false;
}
@property --tw-skew-x {
  syntax: "*";
  inherits: false;
}
@property --tw-skew-y {
  syntax: "*";
  inherits: false;
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-rotate-x: initial;
      --tw-rotate-y: initial;
      --tw-rotate-z: initial;
      --tw-skew-x: initial;
      --tw-skew-y: initial;
      --tw-border-style: solid;
    }
  }
}
